<template>
    <v-card class="pt-4">
        <v-row class="pa-0 ma-0">
            <v-col cols="12" align-self="center" class=" text-center text-h3">
                CONTACTÁNOS
            </v-col>
            <v-col cols="12" offset-md="3" md="6">
                <v-card>
                    <v-card-title>Contactanos directamente por whatsapp</v-card-title>
                    <v-card-text>
                        <v-text-field label="Nombre" hint="Indique su nombre" v-model="name"></v-text-field>
                        <v-text-field label="Apellido" hint="Indique su apellido" v-model="lastname"></v-text-field>
                        <v-text-field label="Email" type="email" hint="Indique su email" v-model="email"></v-text-field>
                        <v-textarea label="Petición" hint="Indique su necesidad a continuación" v-model="message"></v-textarea>
                        <v-btn block color="success" class="my-2" @click="sendMessageToWhatsapp">
                            <v-icon left>phone</v-icon>Whatsapp
                        </v-btn>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-card>
</template>

<script>
export default {
    name:'about-us-view',
    data(){
        return{
            name: '',
            lastname: '',
            email: '',
            message: ''
        }
    },
    methods: {
        sendMessageToWhatsapp(){
            window.location.href = `https://wa.me/5491121776811?text=${this.name && this.lastname ? 'Hola mi nombre es ' + this.name + ' ' + this.lastname + '. ' : ''}${this.message ? 'Tengo la siguiente petición: ' + this.message + '. ': ''}${this.email ? 'Mi email de contacto es: ' + this.email + '.' : ''}`
        }
    }
}
</script>

