<template>
    <div>
        <v-app-bar
            app
            color="primary"
            dark
        >
            <v-app-bar-title>Vuelderjs</v-app-bar-title>
            <v-spacer></v-spacer>
            <v-app-bar-nav-icon @click="menu = true"/>
        </v-app-bar>
        <v-navigation-drawer v-model="menu" temporary right app>
            <v-btn block text v-for="(submenu, index) in submenus" :key="index" @click="goTo(submenu.ref)">
                <v-row class="pl-4">
                    <v-icon left>{{submenu.icon}}</v-icon>{{ submenu.text }}
                </v-row>
            </v-btn>
        </v-navigation-drawer>
    </div>
  </template>
  
  <script>
  export default {
      name: 'mobile-navbar',
      props: {
        refs: {
            type: Object,
            required: true
        }
      },
      data(){
        return {
            menu: false,
            submenus: [
                {
                    ref: 'home',
                    icon: 'home',
                    text: 'Inicio'
                },
                {
                    ref: 'about-us',
                    icon: 'people',
                    text: '¿Quiénes somos?'
                },
                {
                    ref: 'offerings',
                    icon: 'local_offer',
                    text: '¿Qué ofrecemos?'
                },
                {
                    ref: 'tecnology-stack',
                    icon: 'code',
                    text: 'Stack tecnológico'
                },
                {
                    ref: 'prices',
                    icon: 'attach_money',
                    text: 'Nuestros precios'
                },
                {
                    ref: 'contact-us',
                    icon: 'contact_support',
                    text: 'Contáctanos'
                },
                {
                    ref: 'clients',
                    icon: 'business',
                    text: 'Nuestros clientes'
                }
            ]
        }
      },
      methods:{
        goTo(ref){
            const top = this.$store.state.positions[ref]
            window.scrollTo({ top, left: 0, behavior: 'smooth' })
        }
    }
  }
  </script>
  
