<template>
  <v-app-bar
      app
      color="primary"
      dark
    >
      <v-app-bar-title>Vuelderjs</v-app-bar-title>

      <v-spacer></v-spacer>
      <v-btn
        v-for="({ref, icon, text}, index) in submenus" :key="index"
        text
        @click="goTo(ref)"
      >
        <v-icon :left="$vuetify.breakpoint.xlOnly">{{ icon }}</v-icon> {{ $vuetify.breakpoint.xlOnly ? text : '' }}
      </v-btn>
    </v-app-bar>
</template>

<script>
export default {
    name: 'desktop-navbar',
    props: {
        refs: {
            type: Object,
            required: true
        }
    },
    data(){
        return {
            submenus: [
                {
                    ref: 'home',
                    icon: 'home',
                    text: 'Inicio'
                },
                {
                    ref: 'about-us',
                    icon: 'people',
                    text: '¿Quiénes somos?'
                },
                {
                    ref: 'offerings',
                    icon: 'local_offer',
                    text: '¿Qué ofrecemos?'
                },
                {
                    ref: 'tecnology-stack',
                    icon: 'code',
                    text: 'Stack tecnológico'
                },
                {
                    ref: 'prices',
                    icon: 'attach_money',
                    text: 'Nuestros precios'
                },
                {
                    ref: 'contact-us',
                    icon: 'contact_support',
                    text: 'Contáctanos'
                },
                {
                    ref: 'clients',
                    icon: 'business',
                    text: 'Nuestros clientes'
                }
            ]
        }
    },
    methods:{
        goTo(ref){
            const top = this.$store.state.positions[ref]
            window.scrollTo({ top, left: 0, behavior: 'smooth' })
        }
    }
}
</script>

