<template>
  <v-app>
    <desktop-navbar v-if="$vuetify.breakpoint.lgAndUp" :refs="$refs"/>
    <mobile-navbar v-else :refs="$refs"/>

    <v-main>
      <home-view ref="home"/>
      <about-us-view ref="about-us"/>
      <offerings-view ref="offerings"/>
      <tecnology-stack-view ref="tecnology-stack"/>
      <prices-view ref="prices"/>
      <contact-us-view ref="contact-us"/>
      <clients-view ref="clients"/>
    </v-main>
  </v-app>
</template>

<script>
import DesktopNavbar from './components/DesktopNavbar.vue'
import MobileNavbar from './components/MobileNavbar.vue'

//import views
import HomeView from './views/HomeView.vue'
import AboutUsView from './views/AboutUsView.vue'
import OfferingsView from './views/OfferingsView.vue'
import TecnologyStackView from './views/TecnologyStackView.vue'
import PricesView from './views/PricesView.vue'
import ContactUsView from './views/ContactUsView.vue'
import ClientsView from './views/ClientsView.vue'
import { mapMutations } from 'vuex'

export default {
  name: 'App',
  components: {
    DesktopNavbar,
    MobileNavbar,
    HomeView,
    AboutUsView,
    OfferingsView,
    TecnologyStackView,
    PricesView,
    ContactUsView,
    ClientsView
  },
  data(){
    return{

    }
  },
  mounted(){
    window.scrollTo({top: 0, left:0})
    const positions = {}
    const refs = ['home', 'about-us', 'offerings', 'tecnology-stack', 'prices', 'contact-us', 'clients']
    refs.forEach(ref => {
      const view = this.$refs[ref].$el
      positions[ref] = view.getBoundingClientRect().top
    })
    this.setAllPostions(positions)
  },
  methods: {
    ...mapMutations(['setAllPostions'])
  }
};
</script>
